*{
    margin:0;
    padding:0;
    box-sizing:border-box;
}
body{
    background:#3fb1b5;
}
header{
    background:#35b1b5;
    .toggle{
        width: 40px;
        height: 40px;
        position:fixed;
        top:30px;
        right:30px;
        background:#000000;
        display:flex;
        align-items: center;
        justify-content:center;
        span{
            height: 2px;
            background:red;
            position:relative;
            width: 70%;
            border-radius:2px;
            transform:trasnlate(-50%, -50%);
            transition:.5s;
            transition-delay:.5s;
            box-shadow:0 2px 2px rgba(0,0,0,.2);
            &:before, &::after{
                content:'';
                height: 100%;
                width:100%;
                border-radius:2px;
                position:absolute;
                background:red;                
                transition-property: top, transform;
                transition-delay: 0.5s, 0s;
                transition-duration: 0.5s, 0.5s;
            }
            &:before{
                top:-8px;
            }
            &:after{
                top:8px;
            }
        }
    }
    .active{
        span{
            background:transparent;
            &:before{
                top:0;
                transform:rotate(45deg);
                transition-delay: 0s, 0.5s;
            }
            &:after{
                top:0;
                transform:rotate(-45deg);
                transition-delay: 0s, 0.5s;
            }
        }
    }
}
section{
    height:100vh;
}
footer{
    background: #000000;
    color:#ffffff;
    padding: 20px;
    width: 100%;
}